.pop-up-msg {
    width: 200px;
    height: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 2px 1px #dfe1e6;
    border-radius: 3px;
    background-color: white;
    z-index: 20;

    svg {
        > * {
            color: $clr-suzuki-blue;
        }
    }
}
